@import "../../../../global.scss";

.landing {
	height: 100vh;
	width: 100vw;
	position: relative;
	overflow: hidden;
	background: linear-gradient(
			rgba(255, 255, 255, 0.3),
			rgba(255, 255, 255, 0.1)
		),
		url(https://images.unsplash.com/photo-1522202176988-66273c2fd55f?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=3000&q=3000)
			center;
	min-width: 100%;
	background-size: cover;
	background-position: center;

	.wrapper {
		height: 100vh;
		width: 100vw;
		overflow: hidden;
		position: relative;
		display: flex;
		align-items: center;
		justify-content: center;
		position: relative;

		.welcome {
			height: 40vh;
			min-height: 350px;
			width: 40vw;
			padding: 25px 25px;
			border-radius: 15px;
			background: rgb(211, 211, 211);
			background: radial-gradient(
				circle,
				rgba(211, 211, 211, 1) 0%,
				rgba(199, 207, 208, 1) 40%,
				rgba(212, 201, 201, 0.8239670868347339) 60%,
				rgba(219, 219, 219, 0.6194852941176471) 93%
			);
			backdrop-filter: blur(10px);
			-webkit-backdrop-filter: blur(10px);
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
			bottom: 75px;
			position: absolute;
			@include ipad {
				width: 80vw;
				min-height: 10vw;
			}
			@include mobile {
				width: 80vw;
				min-height: 10vw;
			}

			.top {
				display: flex;
				justify-content: center;
				align-items: center;
				font-family: Hind;
				font-size: clamp(0.9rem, 2vw, 4rem);
				font-weight: 400;
				@include ipad {
					font-size: clamp(0.9rem, 4vw, 4rem);
				}
			}

			.center {
				font-family: "Saira", sans-serif;
				display: flex;
				flex-direction: column;
				align-items: center;
				justify-content: center;
				font-weight: 700;
				h2 {
					font-size: clamp(1.2rem, 4vw, 4rem);
					@include ipad {
						font-size: clamp(0.9rem, 8vw, 4rem);
					}
				}

				h3 {
					font-size: clamp(1.2rem, 5vw, 4rem);
					@include ipad {
						font-size: clamp(0.9rem, 10vw, 4rem);
					}
				}
			}

			.bottom {
				font-family: inherit;
				.btn-container {
					height: 50px;
					width: 55vw;
					display: flex;
					flex-direction: row;
					align-items: center;
					justify-content: center;
					overflow: hidden;
					@include ipad {
						height: 100px;
						width: 60vh;
					}
					@include mobile {
						height: 140px;
					}

					.btn {
						margin: 20px;
						padding: 7px;
						font-size: clamp(0.9rem, 1.5vw, 2rem);
						font-family: Hind;
						color: rgb(255, 255, 255);
						background-color: $buttonColor;
						outline: none;
						border-style: none;
						cursor: pointer;
						border-radius: 15px;

						&:hover {
							background-color: $buttonHover;
							transition: all 0.5s ease;
						}
						@include ipad {
							font-size: clamp(0.9rem, 3vw, 4rem);
						}
						@include mobile {
							font-size: clamp(0.9rem, 4vw, 4rem);
							padding: 11px;
							margin: 6px;
						}
					}
				}
			}
		}
	}
}
